import React, { Component } from 'react';
import Work from '../../components/work/Work.js';
import Money from '../../components/money/Money.js';
import './Home.scss'
import {withRouter} from "react-router-dom";

class Home extends Component {
  render() {
    return (
      <div className="Home">
      	<div className="desc">
      		html5: jquery， zepto， react， vue， webpack，threejs，webgl，基于chrome的插件开发<br/>
      		ios: 原生ios客户端简单开发<br/>
      		android: 原生android客户端简单开发<br/>
      		unity: 3d和2d游戏开发<br/>
      		python: 爬虫程序<br/>
      	</div>
          <Work />
          <Money />
      </div>
    );
  }
}

export default withRouter(Home);



